.App {
  text-align: center;
}

.wrapper {
  max-width: 90%;
  width: 350px;
  margin: 0 auto;
  .add-todo-div, .add-todo {
    display: flex;
    border: 1px solid #e9e9e9;
    align-items: center;
    * {
      // padding: .5em;
    }
    input[type="text"] {
      flex-grow: 1;
    }
    svg {
      margin: 0 .15em;
    }
  }
}
.todos-div {
  margin: 0;
  padding: 0;
}

input[type="text"] {
  border: none;
  background: #efefef;
  &:disabled {
  background: #fff;
  border: none;
}
}

input, button {
  padding: .5em;
}

button {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  border: none;
}